.social {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  &__item {
    margin-bottom: 5px;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__link {
    display: block;
    overflow: hidden;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: var(--gray-light);
    transition: color .3s ease;
  }

  &__icon {
    display: block;
    width: 40px;
    height: 37px;

    fill: var(--gray-light);
    object-fit: cover;

    &--github {
      width: 60px;
      height: 60px;
    }
  }
}
